*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.body{
    width: 80%;
    margin: auto;
    font-size: 20px;
    }
 /***********Home***********/
.center{
  padding: 200px 100px;
  text-align: center;
  background-color: hsla(0, 0%, 53%, 0.514);
  top: 0;
  bottom: 0;
}


/***********Form**********/
.sign-form{

    text-align: center ;
    padding: 20px;
    box-shadow: 1px 1px 20px 1px #888888;
    flex: 2;
    }
 .sign-form input{
    border: 1px solid rgba(8, 159, 247, 0.514);
    font-size: 20px;
    padding: 10px;
    border-radius: 5px;
    width: 50%;
    margin: 10px;
    flex: 2;
    }
.sign-form .form{
    background-color: rgba(8, 159, 247, 0.514);
    }
 .contact{
        width: 50%;
        height: 50%;
    }

/*******Links************/
.row{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.signup .links{
    border: 1px solid   #1aa3ff  ;
    padding: 10px;
    margin: 0px 20px;
    text-decoration: none;
    border-radius: 5px;
}
.links{
    text-decoration: none;
    color: #1aa3ff ;
}
.header{
    padding: 30px 0px;
}
.nav{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #1aa3ff;
    color: white;
}
.nav .links{
    padding: 10px 50px;
    color: white;
    font-size: 18px;
    
}

/**********Patnership********/
.image{
    width: 85%;
    height: 30%;
}
/*******Header**********/
.safari{
    margin: 0px;
    padding: 10px;
    border: 1px solid #1aa3ff;
}
.form-search{
    display: flex;
    flex-direction: row;
}
.form-search .links{
    background-color: #1aa3ff;
    color: white;
    padding: 10px 20px;
}

.sides{
    display: flex;
}
.instruct{
    font-size: 25px;
    flex: 1;
}


.row-footer{
    display: flex;
}
.column-footer{
    display: block;
}


/********Footer***********/
.site-footer
{
  background-color:#26272b;
  padding:45px 0 20px;
  font-size:15px;
  line-height:24px;
  color:#737373;
}
.site-footer hr
{
  border-top-color:#bbb;
  opacity:0.5
}
.site-footer hr.small
{
  margin:20px 0
}
.site-footer h6
{
  color:#fff;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}
.site-footer a
{
  color:#737373;
}
.site-footer a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links
{
  padding-left:0;
  list-style:none
}
.footer-links li
{
  display:block
}
.footer-links a
{
  color:#737373
}
.footer-links a:active,.footer-links a:focus,.footer-links a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links.inline li
{
  display:inline-block
}
.site-footer .social-icons
{
  text-align:right
}
.site-footer .social-icons a
{
  width:40px;
  height:40px;
  line-height:40px;
  margin-left:6px;
  margin-right:0;
  border-radius:100%;
  background-color:#33353d
}
.copyright-text
{
  margin:0
}
@media (max-width:991px)
{
  .site-footer [class^=col-]
  {
    margin-bottom:30px
  }
}
@media (max-width:767px)
{
  .site-footer
  {
    padding-bottom:0
  }
  .site-footer .copyright-text,.site-footer .social-icons
  {
    text-align:center
  }
}
.social-icons
{
  padding-left:0;
  margin-bottom:0;
  list-style:none
}
.social-icons li
{
  display:inline-block;
  margin-bottom:4px
}
.social-icons li.title
{
  margin-right:15px;
  text-transform:uppercase;
  color:#96a2b2;
  font-weight:700;
  font-size:13px
}
.social-icons a{
  background-color:#eceeef;
  color:#818a91;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:44px;
  height:44px;
  text-align:center;
  margin-right:8px;
  border-radius:100%;
  -webkit-transition:all .2s linear;
  -o-transition:all .2s linear;
  transition:all .2s linear
}
.social-icons a:active,.social-icons a:focus,.social-icons a:hover
{
  color:#fff;
  background-color:#29aafe
}
.social-icons.size-sm a
{
  line-height:34px;
  height:34px;
  width:34px;
  font-size:14px
}
.social-icons a.facebook:hover
{
  background-color:#3b5998
}
.social-icons a.twitter:hover
{
  background-color:#00aced
}
.social-icons a.linkedin:hover
{
  background-color:#007bb6
}
.social-icons a.dribbble:hover
{
  background-color:#ea4c89
}
.row{
  display: flex;
}


.border{
  border: 1px solid #1aa3ff   ;
  width: 50px;
  height: 30px;
  background-color:#1aa3ff ;
  color: white;
}

.logo{
  width: 200px;
  height: 200px;
}





@media (max-width:767px)
{
  .social-icons li.title
  {
    display:block;
    margin-right:0;
    font-weight:600
  }
}